import React from 'react';
import ReactDOM from 'react-dom';
import Main from './main.js';
import Result from './result.js';

class App extends React.Component{

  constructor() {
    super();
    this.state = {bool:true,
                  score:null,
                  Youken:null}
  }

  managementState(e){
    
    this.setState({score:e.score,
                   Youken:e.Youken,
                   bool:e.bool,
    });

  }

  
  render(){

    let Currentpage;

    switch (this.state.bool) {
      case true:
      Currentpage = (() => <Main getResult={this.managementState.bind(this)}/>);
      break;
      case false:
      Currentpage = (() => <Result/>);
      break;
      default:

    }

    return (
      <Currentpage/>
    )

   }
  }

export default App;
