import React, { Component } from 'react';
import Filefunc from './filefn';


export default class FileForm extends React.Component {
  constructor() {
    super();
  }

  getChild(e) {

    let filefunction = new Filefunc();
    
    // 入力された成績ファイルを二次元配列にして親クラスのstateに格納
    filefunction.Getdata(e, (data) => {
      this.props.hoge(data);
    });

  }

  render() {
    return (
      <React.Fragment>
      
      <div className="form-group"><label htmlFor="subject">成績データ</label>
      <div className="custom-file"><input type="file" className="custom-file-input" onChange={this.getChild.bind(this)}/><label className="custom-file-label">Upload File</label></div>
      </div>
      
      </React.Fragment>
    )
  }

}





